"use client";

import "./css/main.css";
import { useState, useEffect, useRef, useContext } from "react";
import { Translation } from "./components/translation";
import { LanguageContext } from "./components/LanguageContext";
import { getCookie } from "./api/cookies";
import axios from 'axios';

export default function Home() {
    const { language } = useContext(LanguageContext);
    const t = Translation[language];
    const [isLoggedIn, setIsLoggedIn] = useState(false);
    const [formData, setFormData] = useState({
        name: '',
        email: '',
        message: '',
    });
    const [formError, setFormError] = useState('');
    const [formSuccess, setFormSuccess] = useState('');

    useEffect(() => {
        const loggedIn = getCookie("isLoggedIn") === "true";
        setIsLoggedIn(loggedIn);
    }, []);

    const handleInputChange = (e) => {
        const { id, value } = e.target;
        setFormData({
            ...formData,
            [id]: value,
        });
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        setFormError('');
        setFormSuccess('');

        const { name, email, message } = formData;

        if (!name || !email || !message) {
            setFormError(t.allFieldsRequired || "All fields are required");
            return;
        }

        try {
            const response = await axios.post('/api/send-email', { name, email, message });

            if (response.status === 200) {
                setFormSuccess(t.messageSentSuccess || "Message sent successfully!");
                setFormData({ name: '', email: '', message: '' }); // Clear form
            } else {
                setFormError(response.data.message || t.messageSentError || "Failed to send message");
            }
        } catch (error) {
            setFormError(error.response?.data?.message || t.messageSentError || "Failed to send message");
        }
    };
    
    useEffect(() => {
        const loggedIn = getCookie("isLoggedIn") === "true";
        setIsLoggedIn(loggedIn);
    }, []);

    const sliderRef = useRef(null);
    const slidesRef = useRef([]);
    const [currentIndex, setCurrentIndex] = useState(0);
    const [isPaused, setIsPaused] = useState(false);

    const togglePause = () => {
        setIsPaused((prev) => !prev);
    };

    const moveRight = () => {
        const nextIndex = (currentIndex + 1) % slidesRef.current.length;
        setCurrentIndex(nextIndex);
    };

    const moveLeft = () => {
        const prevIndex = (currentIndex - 1 + slidesRef.current.length) % slidesRef.current.length;
        setCurrentIndex(prevIndex);
    };

    useEffect(() => {
        const interval = setInterval(() => {
            if (!isPaused) {
                moveRight();
            }
        }, 2000);

        return () => clearInterval(interval);
    }, [currentIndex, isPaused]);

    useEffect(() => {
        if (sliderRef.current) {
            const xValue = -200 * currentIndex;
            sliderRef.current.style.transform = `translateX(calc(50% - 140px + ${xValue}px))`;
        }
    }, [currentIndex]);

    const [showArrow, setShowArrow] = useState(false);

    useEffect(() => {
        const handleScroll = () => {
            if (window.scrollY >= 600) {
                setShowArrow(true);
            } else {
                setShowArrow(false);
            }
        };

        window.addEventListener("scroll", handleScroll);

        return () => {
            window.removeEventListener("scroll", handleScroll);
        };
    }, []);

    const scrollToTop = () => {
        window.scrollTo({
            top: 0,
            behavior: "smooth",
        });
    };

    return (
        <>
            {showArrow && (
                <div id="arrow" onClick={scrollToTop}>
                    <img src="/icons/arrow-up-solid.svg" alt="Scroll to top" />
                </div>
            )}

            <div id="home" className="content">
                <div className="overlay"></div>
                <div className="imgbox">
                    <img src="/imgs/homeimg.jpg" alt="مدرسة د.محمد حسن الزيات" />
                </div>
                <div className="text-content">
                    <h2>{t.welcome}</h2>
                    <a href="#about" className="btn-primary">
                        {t.aboutUs} <img src="/icons/address-card-solid.svg" alt="address card image" />
                    </a>
                </div>
            </div>

            <main>
                <section id="about">
                    <div className="container">
                        <h2>{t.aboutUs}</h2>
                        <hr />
                        <div id="about-content">
                            <div className="col-1">
                                <h3>
                                    <span className="text-primary">{t.schoolName}</span>
                                </h3>
                                <p id="discription">{t.schoolDescription}</p>
                            </div>
                            <div className="col-2">
                                <p className="social">
                                    <b>{t.socialMedia}</b>{" "}
                                    <a
                                        href="https://www.facebook.com/share/g/194v4Ry7qw/"
                                        target="_blank"
                                    >
                                        <img src="/icons/facebook-brands.svg" alt="Facebook" />
                                    </a>
                                </p>
                                <p id="place">
                                    <b>{t.place}</b>{" "}
                                    <a href="https://maps.app.goo.gl/isxUtNNcwRhijkTa6" target="_blank">
                                        <img src="/icons/location-dot-solid.svg" alt="Location" />
                                    </a>{" "}
                                    {t.location}
                                </p>
                            </div>
                        </div>
                    </div>
                </section>

                <section id="skills">
                    <div className="container">
                        <h2>
                            {t.skillsTitle.n1} <span className="text-primary">{t.skillsTitle.n2}</span>
                        </h2>
                        <hr />
                        <br />
                        <div id="slider">
                            <ul id="slul" ref={sliderRef}>
                                {[...Array(12)].map((_, index) => (
                                    <li
                                        key={index}
                                        className={`slide ${currentIndex === index ? "active" : ""}`}
                                        ref={(el) => (slidesRef.current[index] = el)}
                                    >
                                        <img src={`/imgs/skills/${index + 1}.jpg`} alt={`img ${index + 1}`} />
                                    </li>
                                ))}
                            </ul>
                        </div>
                        <div className="controls">
                            <button id="arrow-left" className="controls-btns" onClick={moveLeft}>
                                <img src="/icons/arrow-up-solid.svg" style={{ transform: 'rotate(-90deg)' }} alt="Left arrow" />
                            </button>
                            <button id="turn-on-off" onClick={togglePause} className="controls-btns">
                                {isPaused ? (
                                    <img src="/icons/play-solid.svg" alt="Play" />
                                ) : (
                                    <img src="/icons/pause-solid.svg" alt="Pause" />
                                )}
                            </button>
                            <button id="arrow-right" className="controls-btns" onClick={moveRight}>
                                <img src="/icons/arrow-up-solid.svg" style={{ transform: 'rotate(90deg)' }} alt="Right arrow" />
                            </button>
                        </div>
                    </div>
                </section>

                <div id="contactus">
                <div className="container">
                    <h2>{t.contactTitle}</h2>
                    <hr />
                    <div className="contact-content">
                        <div className="contactimgbox">
                            <img src="/imgs/contact_us.png" alt="Contact us" />
                        </div>
                        <div className="contact-text">
                            <form className="form" onSubmit={handleSubmit}>
                                <div className="descr">
                                    <p>{t.contactTitle}</p>
                                </div>
                                <div className="input">
                                    <input
                                        required
                                        id="name"
                                        type="text"
                                        value={formData.name}
                                        onChange={handleInputChange}
                                    />
                                    <label htmlFor="name">{t.name}</label>
                                </div>
                                <div className="input">
                                    <input
                                        required
                                        id="email"
                                        type="email"
                                        value={formData.email}
                                        onChange={handleInputChange}
                                    />
                                    <label htmlFor="email">{t.email}</label>
                                </div>
                                <div className="input">
                                    <textarea
                                        required
                                        cols="30"
                                        rows="1"
                                        id="message"
                                        value={formData.message}
                                        onChange={handleInputChange}
                                    ></textarea>
                                    <label htmlFor="message">{t.message}</label>
                                </div>
                                <button type="submit" className="btn-primary">
                                    {t.sendMessage}
                                </button>
                            </form>
                            {formError && <p className="error">{formError}</p>}
                            {formSuccess && <p className="success">{formSuccess}</p>}
                        </div>
                    </div>
                </div>
            </div>
            </main>
        </>
    );
}
